
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'secondStep',
components: {
XTextarea,
Group
},
data () {
return {
value: '',
value2: '',
baseInfo: {}
}
},
created () {
this.getBaseData()
},
methods: {
sele(index,status) {
this.baseInfo.requirements[index].checkedStatus = status
},
getBaseData() {
this.$http.fetchGet('/merchant/room/get/introduceInfo',{roomId: this.$route.query.params.id}).then((res)=>{
this.baseInfo = res.data.data
})
},
getBack () {
this.$router.replace({
name: 'houseAdd',
query: {
params: this.$route.query.params
}
})
},
getNextStep () {
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/room/add/introduceInfo',this.baseInfo).then((res)=>{
this.$vux.loading.hide()
this.$router.replace({
name: 'thirdStep',
query: {
params: this.$route.query.params
}
})
})
}
}
}



